export default "import Step from 'component/Step'\nimport { useState } from 'react'\nimport styles from './index.less'\nconst options = [\n    { content: '上传Excel' },\n    { content: '解析Excel' },\n    { content: '写入数据' },\n    { content: '测试数据' },\n]\nconst StepTagView = () => {\n    const [step, setStep] = useState(0)\n    return (\n        <div>\n            <Step\n                options={options}\n                step={step}\n                onSelected={setStep}\n            />\n        </div>\n    )\n}\n\nexport default StepTagView;";