import classNames from 'classnames'
import { useState } from 'react'
import styles from './index.less'

const options = [
    { content: '上传Excel' },
    { content: '解析Excel' },
    { content: '写入数据' },
    { content: '测试数据' },
]

interface OptionsConfig {
    content:string
}

interface Props {
    options: OptionsConfig[];
    step: number;
    onSelected: (key:number) => void
}

const Step = (props: Props) => {
    const { step, options, onSelected } = props;
    return (
        <div className={styles.container}>{
            options.map((item, index) => {
                return (
                    <div 
                    key={index} 
                    onClick={() => {
                        if(step !== index) {
                            onSelected(index)
                        }
                    }}
                    className={ 
                        classNames(styles.item, {
                            [styles.active]: index === step,
                            [styles.doneStyle]: index < step
                        })}>
                        {item.content}
                    </div>
                )
            }, [])
            }</div>
    )
}

export default Step